import React, { useEffect, useState } from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { Box } from "@mui/material";
import CardActions from "@mui/material/CardActions";
import Button from "@mui/material/Button";
// Api
import { fetchOpenWeatherData, } from "../../utils/api";
const WeatherCardContainer = ({ children, onDelete }) => {
    return (React.createElement(Box, { mx: "4px", my: "16px" },
        React.createElement(Card, null,
            React.createElement(CardContent, null, children),
            React.createElement(CardActions, null, onDelete && React.createElement(Button, { onClick: onDelete }, "Delete")))));
};
const WeatherCard = ({ city, onDelete, tempScale }) => {
    const [weatherData, setWeatherData] = useState(null);
    const [cardState, setCardState] = useState("loading");
    useEffect(() => {
        fetchOpenWeatherData(city, tempScale)
            .then((data) => {
            setWeatherData(data);
            setCardState("ready");
        })
            .then((data) => {
            setWeatherData(data);
        })
            .catch((err) => setCardState(err));
    }, [city, tempScale]);
    if (cardState === "loading" || cardState == "error") {
        return (React.createElement(WeatherCardContainer, { onDelete: onDelete },
            React.createElement(Typography, { variant: "body1" }, cardState == "loading"
                ? "Loading..."
                : "Error: Could not take information fo this city")));
    }
    return (React.createElement(WeatherCardContainer, { onDelete: onDelete },
        React.createElement(Typography, { variant: "h5" }, weatherData.name),
        React.createElement(Typography, { variant: "body1" }, Math.round(weatherData.main.temp)),
        React.createElement(Typography, { variant: "body1" },
            "Feels like:",
            Math.round(weatherData.main.feels_like),
            " ")));
};
export default WeatherCard;
